import R from 'ramda';

export function findSelectedRows(existing_rows, existing_row_indices, new_rows) {
    return existing_row_indices.map(
        i => R.findIndex(R.equals(existing_rows[i]), new_rows)
    );
}

export function filterIndices(some_rows, all_rows) {
    return some_rows.map(
        some_row => R.findIndex(R.equals(some_row), all_rows)
    );
}



